
.StampContainerHeader{
    font-size: larger;
    margin-bottom: 6px;
    text-align: left;
    color:whitesmoke;
    height: 20px;    
    align-items: center;
    
    margin-left: 3px;
}   

.HeaderCol{
    height: inherit;
    margin-left: 3px;
    margin-right: 3px;

}



.HeaderCol img{
    
      max-height:100%;
    background-color: khaki;
}
.StampContainerImage{
    background-color:var(--main-bg-color);
    padding: 3px;
    height: auto;
   
}

.StampContainerImage img{
    max-width: 100%;
    max-height: 100%;
    /* max-height: 360px; */
    /* max-width: 100%; */
    /* min-height: 100px;
    min-width: 100px; */
}

.StampContainer{
    margin: 3px;
    
    border: black;
    border-width: 1px;
    border-style:solid;
    border-radius: 25px ;
    padding-bottom: 5px;
    background-image: linear-gradient(rgb(158, 158, 158), rgb(85, 85, 85));

}

.StampContainerData{
   
    text-align: left;
 
    
    

}

.StampDataRowHeader{
    background-color: darkgrey;
    text-align: left;
    font-size: xx-small;
    padding: 2px;
    text-align: center;
    border-radius: 10px 0px 0px 0px;
    margin: 0px 2px 0px ;
    
}

.StampDataRow{
    background-color: coral;
    text-align: left;
    padding: 2px;
    margin: 0px 2px 4px 2px;
    border-style: solid;
    border-width: 2px 0px 2px 0px;
    border-color: black;
    
}

